'use client';

import { useLanguage } from '@/app/context/language-context';

import { ExpenseChart } from '@/components/dashboard/expense-chart';
import { CategoryChart } from '@/components/dashboard/category-chart';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';

export default function ReportsPage() {
    const { t } = useLanguage();

    const monthlyData = [
        { month: 'Tháng 1', income: 15000000, expense: 8500000, balance: 6500000 },
        { month: 'Tháng 2', income: 16000000, expense: 9200000, balance: 6800000 },
        { month: 'Tháng 3', income: 14500000, expense: 7800000, balance: 6700000 },
        { month: 'Tháng 4', income: 18000000, expense: 12000000, balance: 6000000 },
        { month: 'Tháng 5', income: 17500000, expense: 9500000, balance: 8000000 },
        { month: 'Tháng 6', income: 19000000, expense: 10500000, balance: 8500000 },
    ];

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h1 className="text-3xl font-bold tracking-tight text-gradient">
                    {t('Reports')}
                </h1>
            </div>

            <div className="grid gap-6 md:grid-cols-2">
                <ExpenseChart />
                <CategoryChart />
            </div>

            <Card className="border-0 shadow-md">
                <CardHeader>
                    <CardTitle>Chi tiết theo tháng</CardTitle>
                </CardHeader>
                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Tháng</TableHead>
                                <TableHead className="text-right">Thu nhập</TableHead>
                                <TableHead className="text-right">Chi tiêu</TableHead>
                                <TableHead className="text-right">Số dư</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {monthlyData.map((row) => (
                                <TableRow key={row.month}>
                                    <TableCell className="font-medium">{row.month}</TableCell>
                                    <TableCell className="text-right text-green-600">{row.income.toLocaleString('vi-VN')}đ</TableCell>
                                    <TableCell className="text-right text-red-600">{row.expense.toLocaleString('vi-VN')}đ</TableCell>
                                    <TableCell className="text-right font-bold text-blue-600">{row.balance.toLocaleString('vi-VN')}đ</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
        </div>
    );
}
